<template>
  <div class="hello">
    <h1>{{ msg }}</h1><br><br>
    <!--<h2>Essential Links</h2>-->
    <ul>
      <li>
        <a
          href="https://listenfeng.github.io/myblog/"
          target="_blank"
        >
          我的学习笔记
        </a>
      </li>
      <li>
        <a
          href="https://listenfeng.github.io/myblog/"
          target="_blank"
        >
          有没有你喜欢的效果
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Hello',
  data () {
    return {
      msg: 'Welcome to my github page'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
  font-size:18px;
}
  .hello{
    width:100%;
    height:100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(lavenderblush,navajowhite);
  }
</style>



